'use client';

import { useState, useEffect } from 'react';
import GameBoard from './components/GameBoard';
import { generateSecretNumber } from './utils/gameLogic';
import { raleway } from './layout';

export default function Home() {
  const [secretNumber, setSecretNumber] = useState('');
  const [gameOver, setGameOver] = useState(false);
  const [won, setWon] = useState(false);

  useEffect(() => {
    setSecretNumber(generateSecretNumber());
  }, []);

  const startNewGame = () => {
    setSecretNumber(generateSecretNumber());
    setGameOver(false);
    setWon(false);
  };

  return (
    <main className="min-h-screen bg-[#F8FAFF] p-6">
      {/* Main Content */}
      <div className="max-w-md mx-auto">
        <h1 className={`text-[#1E2B4F] text-3xl mb-8 ${raleway.className}`}>
          NumDoku
        </h1>

        {/* Game Container */}
        <div className="bg-white rounded-3xl shadow-[0_4px_20px_rgba(197,206,231,0.25)] p-6">
          <GameBoard 
            secretNumber={secretNumber}
            onGameOver={() => setGameOver(true)}
            onWin={() => setWon(true)}
          />
          
          {(gameOver || won) && (
            <div className="mt-6 text-center">
              <p className="text-xl mb-4 text-[#1E2B4F]">
                {won ? 'Congratulations! You won! ðŸŽ‰' : 'Game Over!'}
                {gameOver && !won && <div>The number was {secretNumber}</div>}
              </p>
              <button
                onClick={startNewGame}
                className="bg-[#1E2B4F] text-white px-6 py-3 rounded-2xl shadow-lg hover:bg-[#2a3b6a] transition-all"
              >
                Play Again
              </button>
            </div>
          )}
        </div>
      </div>
    </main>
  );
}