"use client";

import { useState } from "react";
import { checkGuess } from "../utils/gameLogic";
import Dialog from "./Dialog";

interface GuessResult {
  guess: string;
  correctNumbers: number;
  correctPositions: number;
}

interface GameBoardProps {
  secretNumber: string;
  onGameOver: () => void;
  onWin: () => void;
}

export default function GameBoard({
  secretNumber,
  onGameOver,
  onWin,
}: GameBoardProps) {
  const [currentGuess, setCurrentGuess] = useState("");
  const [guesses, setGuesses] = useState<GuessResult[]>([]);
  const [dialog, setDialog] = useState<{
    isOpen: boolean;
    title: string;
    message: string;
  }>({
    isOpen: false,
    title: "",
    message: "",
  });
  const maxTries = 8;

  const hasRecurringDigits =
    currentGuess.length === 4 && new Set(currentGuess).size !== 4;

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (currentGuess === secretNumber) {
      setDialog({
        isOpen: true,
        title: "Congratulations!",
        message: "You won! You guessed the correct number!",
      });
      onWin();
    } else {
      const result = checkGuess(currentGuess, secretNumber);
      const newGuesses = [{ guess: currentGuess, ...result }, ...guesses];
      setGuesses(newGuesses);
      setCurrentGuess("");

      if (newGuesses.length >= maxTries) {
        setDialog({
          isOpen: true,
          title: "Game Over",
          message: `You've run out of tries! The correct number was ${secretNumber}.`,
        });
        onGameOver();
      }
    }
  };

  const resetGame = () => {
    setCurrentGuess("");
    setGuesses([]);
    setDialog({ isOpen: false, title: "", message: "" });
  };
  console.log(guesses);
  return (
    <div className="space-y-6">
      {!dialog.isOpen && (
        <>
          <form onSubmit={handleSubmit}>
            <div className="flex flex-col gap-2">
              <div className="flex gap-3">
                <input
                  type="text"
                  value={currentGuess}
                  onChange={(e) => setCurrentGuess(e.target.value.slice(0, 4))}
                  className="flex-1 p-3 rounded-2xl bg-[#F8FAFF] shadow-[inset_0_2px_4px_rgba(197,206,231,0.25)] text-[#333] text-lg focus:outline-none border border-[#E7ECF3]"
                  placeholder="Enter 4 unique digits"
                  maxLength={4}
                  minLength={4}
                />
                <button
                  type="submit"
                  className="bg-[#4D77FF] text-white px-6 py-3 rounded-2xl shadow-lg hover:bg-[#3D67FF] transition-all disabled:opacity-50 disabled:cursor-not-allowed"
                  disabled={
                    currentGuess.length !== 4 ||
                    !/^\d+$/.test(currentGuess) ||
                    hasRecurringDigits
                  }
                >
                  Try
                </button>
              </div>
              {hasRecurringDigits && (
                <div className="text-red-500">Please use unique digits</div>
              )}
            </div>
          </form>
          <div className="mt-4 text-[#333]">
            Remaining tries: {maxTries - guesses.length}
          </div>
          <div className="space-y-2">
            <div className="grid grid-cols-3 gap-4 font-bold mb-2 text-[#333]">
              <div>Guess</div>
              <div>Correct #</div>
              <div>Position</div>
            </div>
            {guesses.reverse().map((guess, index) => (
              <div key={index} className="grid grid-cols-3 gap-4 py-2">
                <div>{guess.guess}</div>
                <div>{guess.correctNumbers}</div>
                <div>{guess.correctPositions}</div>
              </div>
            ))}
          </div>
        </>
      )}

      <Dialog
        isOpen={dialog.isOpen}
        title={dialog.title}
        message={dialog.message}
        onClose={() => {
          setDialog({ ...dialog, isOpen: false });
          resetGame();
        }}
      />
    </div>
  );
}
